*{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body{
  padding: 5%;
}

input{
  color: grey;
  width: 100%;
  font-size: 1.3rem;
  border: none;
  outline: none;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

}

.card {
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  border-radius: 6px;
  overflow: hidden;
  width: 500px;
  height: auto;
  margin-bottom: 10px;
  margin-right: 5px;
  flex-wrap: nowrap;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  width: 100%;
}